import React from "react"
import CardresSC from "./CardresSC"
import { graphql, useStaticQuery } from "gatsby"

const query = graphql`
  {
    allContentfulCursos {
      nodes {
        titulo
        miniResumen {
          miniResumen
        }
        enOferta
        image {
          gatsbyImageData(width: 300, placeholder: BLURRED, layout: FULL_WIDTH)
        }
        url
      }
    }
  }
`

const TresCursosMas = () => {
  const { allContentfulCursos } = useStaticQuery(query)
  const { nodes } = allContentfulCursos
  return (
    <section className="bg-neutral">
      <div className="max-w-screen-xl px-4 py-8 mx-auto">
        <div>
          <span className="inline-block w-full h-0.5 bg-white"></span>
          <h2 className="text-white mt-1 text-2xl font-extrabold tracking-wide uppercase lg:text-3xl">
            Cursos
          </h2>
        </div>

        <div className="grid grid-cols-1 mt-8 lg:grid-cols-3 gap-x-3 gap-y-9">
          {nodes.map((a, i) => (
            <CardresSC
              key={i}
              imagen={a.image.gatsbyImageData}
              titulo={a.titulo}
              miniResumen={a.miniResumen.miniResumen}
              url={a.url}
            />
          ))}
        </div>
      </div>
    </section>
  )
}

export default TresCursosMas
